import React, { Component } from 'react'


import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faHtml5 } from '@fortawesome/free-brands-svg-icons';
import { faJsSquare } from '@fortawesome/free-brands-svg-icons';
import { faCss3Alt } from '@fortawesome/free-brands-svg-icons';
import { faWordpress } from '@fortawesome/free-brands-svg-icons';
import { faPhp } from '@fortawesome/free-brands-svg-icons';
import { faGithubSquare } from '@fortawesome/free-brands-svg-icons';
import { faAws } from '@fortawesome/free-brands-svg-icons';
import { faBootstrap } from '@fortawesome/free-brands-svg-icons';

import { faJoomla} from '@fortawesome/free-brands-svg-icons';
import { faNpm} from '@fortawesome/free-brands-svg-icons';
import { faLess } from '@fortawesome/free-brands-svg-icons';
import { faNode } from '@fortawesome/free-brands-svg-icons';
import { faReact } from '@fortawesome/free-brands-svg-icons';
import { faSass } from '@fortawesome/free-brands-svg-icons';




import { faStar } from '@fortawesome/free-solid-svg-icons';

export class Skills extends Component {



    
    render() {

        let Skill =(props) => {
         

            return (
     
                    <div className="outer-skills">
                        <div className="logo">
                            <FontAwesomeIcon icon={ props.tech} size="4x"/> 
                        </div>
                        <div className="stars">
                            <FontAwesomeIcon icon={ props.star1 } /> 
                            <FontAwesomeIcon icon={ props.star2 } /> 
                            <FontAwesomeIcon icon={ props.star3 } /> 
                            <FontAwesomeIcon icon={ props.star4 } /> 
                            <FontAwesomeIcon icon={ props.star5 } /> 
                        </div>
                    </div>
             
              
          )
        }

        return (
            <div className="container h-100 bg-light">

                <div className="d-flex flex-column justify-content-center align-content-center">
                <h2 className="text-center my-3">Skills-set</h2>
                <div className="fullskillset">
                <Skill tech={ faHtml5 }         star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }   star5= { faStar } />
                <Skill tech={ faJsSquare }      star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }   star5= { faStar }   />
                <Skill tech={ faCss3Alt }       star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }   star5= { faStar }   />
                <Skill tech={ faWordpress }     star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }  />
                <Skill tech={ faGithubSquare }  star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }  />
                <Skill tech={ faBootstrap }     star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }  />
                <Skill tech={ faNpm }           star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }  />
                <Skill tech={ faSass }          star1= { faStar }  star2= { faStar }    star3= { faStar }   star4= { faStar }  />
                <Skill tech={ faPhp }           star1= { faStar }  star2= { faStar }    star3= { faStar } />
                <Skill tech={ faAws }           star1= { faStar }  star2= { faStar }    star3= { faStar } />
                <Skill tech={ faJoomla}         star1= { faStar }  star2= { faStar }    star3= { faStar } />
                <Skill tech={ faLess }          star1= { faStar }  star2= { faStar }    star3= { faStar } />
                <Skill tech={ faReact }         star1= { faStar }  star2= { faStar }    star3= { faStar } />
                <Skill tech={ faNode }          star1= { faStar }  star2= { faStar }   />
                </div>
                </div>
            </div>

            
        )
    }
    
}

export default Skills



